.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
*,
::after,
::before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

html {
    font-size: 625%;
}

body {
    font-family: HelveticaNeue-Thin;
    font-size: .14rem;
    color: #333;
    background-color: #fff;
    -webkit-tap-highlight-color: transparent;
    /* 移动端取消点击蓝色 */
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
form {
    margin: 0;
}

a:hover,
a:focus,
a:active {
    text-decoration: none;
}

input,
button,
select,
textarea {
    padding: 0;
    background: transparent;
    border: none;
    outline: none;
    /* appearance: none; */
}

a,
button,
select {
    cursor: pointer;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type="number"] {
    -moz-appearance: textfield;
}

/* 清除input和button样式 */

input,
button {
    padding: 0;
    border: none;
    background: none;
    outline: none;
}

/*image with no-border*/

img {
    vertical-align: middle;
    border: none;
}